.options {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.options__item {
  position: relative;
  cursor: pointer;
}
.options__item:not(:last-child) {
  margin-bottom: em(5);
}
.options__input {
  width: 0;
  height: 0;
  opacity: 0;
  position: absolute;
}
.options__input:focus + .options__label:before {
  box-shadow: 0 0 5px #000;
}
.options__input:checked + .options__label:after {
  transform: scale(1);
}
.options__label {
  display: inline-flex;
  align-items: center;
  cursor: pointer;
  gap: 10px;
}
.options__label:before {
  content: "";
  align-self: flex-start;
  width: 20px;
  height: 20px;
  flex: 0 0 20px;
  border-radius: 50%;
  border: 1px solid #a7a9ac;
}
.options__label:after {
  content: "";
  transition: all 0.3s ease 0s;
  transform: scale(0);
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: #77243a;
  position: absolute;
  left: 5px;
  top: 5px;
}
.burger {
  cursor: pointer;
  border: none;
  background-color: #fff;
  border-radius: 5px;
  padding: 14px 14px 15px 15px;
  position: relative;
  z-index: 1000;
}
.burger__inner {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 5px;
}
.burger__circle {
  width: 8px;
  height: 8px;
  display: inline-block;
  background-color: #325f93;
  border-radius: 50%;
}
.burger__circle:nth-child(2) {
  background-color: #1dc46c;
}
.burger--active .burger__circle {
  background-color: #325f93;
  animation: burger-circle 1s infinite;
}
.burger--active .burger__circle:nth-child(1) {
  animation-delay: 0.75s;
}
.burger--active .burger__circle:nth-child(2) {
  background-color: #1dc46c;
}
.burger--active .burger__circle:nth-child(3) {
  animation-delay: 0.5s;
}
.burger--active .burger__circle:nth-child(4) {
  animation-delay: 0.25s;
}

@keyframes burger-circle {
  0% {
    background-color: #1dc46c;
  }
  100% {
    background-color: #325f93;
  }
}
